
"use client";

import { Card, CardContent } from "@/components/ui/card";
import { Users, User, Hourglass, Calendar, ChevronLeft, ChevronRight, BarChart3, UserPlus, Upload, FileDown } from "lucide-react";
import { useState, useEffect, useMemo, useCallback } from "react";
import { Semester, SessionStudent, Session } from "@/lib/types";
import { useAuth } from "@/context/auth-context";
import { Loader2 } from "lucide-react";
import { Separator } from "@/components/ui/separator";
import { Button } from "@/components/ui/button";
import { cn } from "@/lib/utils";
import { db } from "@/lib/firebase";
import { collection, onSnapshot, doc } from "firebase/firestore";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { ToggleGroup, ToggleGroupItem } from "@/components/ui/toggle-group"
import { Badge } from "@/components/ui/badge";

const ScheduleGrid = ({ semester, teacher }: { semester: Semester | undefined, teacher: string }) => {
  const days = ["Saturday", "Sunday", "Monday", "Tuesday", "Wednesday", "Thursday"];
  const timeSlots = Array.from({ length: 13 }, (_, i) => `${i + 9}:00`); // 9am to 9pm
  const { user } = useAuth();
  
  const [localSchedule, setLocalSchedule] = useState<Record<string, Session[]>>({});

  useEffect(() => {
    if (semester?.masterSchedule?.[teacher]) {
      setLocalSchedule(structuredClone(semester.masterSchedule[teacher]));
    } else {
      setLocalSchedule({});
    }
  }, [semester, teacher]);

  if (!semester || !teacher) {
    return <Card className="mt-4"><CardContent className="p-4 text-center text-muted-foreground">Please select a semester and a teacher to view the schedule.</CardContent></Card>;
  }
  
  const getSessionForSlot = (day: string, time: string) => {
    const daySchedule = localSchedule[day];
    if (!daySchedule) return null;
    
    const hour = parseInt(time.split(':')[0]);
    
    return daySchedule.find(session => {
        const sessionTimeMatch = session.time.match(/(\d+):00 (AM|PM)/);
        if (!sessionTimeMatch) { 
            const sessionStartHour24 = parseInt(session.time.split(':')[0]);
            return hour === sessionStartHour24;
        };

        let sessionStartHour = parseInt(sessionTimeMatch[1]);
        const isPM = sessionTimeMatch[2] === 'PM';

        if (isPM && sessionStartHour !== 12) {
            sessionStartHour += 12;
        }
        if (!isPM && sessionStartHour === 12) { 
            sessionStartHour = 0;
        }
        return hour === sessionStartHour;
    });
  }
  
  const getRowSpan = (session: any) => {
      return session.duration || 1;
  }

  const formatTimeForDisplay = (time: string) => {
      const date = new Date(`1970-01-01T${time}:00`);
      return date.toLocaleTimeString('en-US', { hour: 'numeric', hour12: true });
  }

  return (
    <div className="grid grid-cols-[auto_1fr] md:grid-cols-[auto_repeat(6,_1fr)] gap-px -ml-4 -mr-4 bg-border">
      {/* Time Column */}
      <div className="flex flex-col">
        <div className="h-12 bg-background"></div>
        {timeSlots.map(time => (
            <div key={time} className="h-28 flex items-start justify-center bg-background pt-2 px-2 text-xs text-muted-foreground">
                {formatTimeForDisplay(time)}
            </div>
        ))}
      </div>

      {/* Day Columns */}
      {days.map(day => (
        <div key={day} className="col-span-1 bg-background">
          <div className="sticky top-16 z-10 bg-background/95 backdrop-blur-sm h-12 flex items-center justify-center font-semibold border-b">
            {day}
          </div>
          <div className="grid grid-rows-[repeat(13,_7rem)] gap-px">
            {timeSlots.map((time) => {
              const session = getSessionForSlot(day, time);
              if (session) {
                 const currentHour = parseInt(time.split(':')[0]);
                 const sessionTimeMatch = session.time.match(/(\d+):00 (AM|PM)/);
                 let sessionStartHour = -1;
                 
                 if (sessionTimeMatch) {
                    sessionStartHour = parseInt(sessionTimeMatch[1]);
                    const isPM = sessionTimeMatch[2] === 'PM';
                    if (isPM && sessionStartHour !== 12) sessionStartHour += 12;
                    if (!isPM && sessionStartHour === 12) sessionStartHour = 0;
                 } else {
                    sessionStartHour = parseInt(session.time.split(':')[0]);
                 }
                
                 if (currentHour > sessionStartHour) return null;


                 return (
                    <div
                      key={`${day}-${time}`}
                      className="bg-background border-t p-1 flex flex-col"
                      style={{ gridRow: `span ${getRowSpan(session)} / span ${getRowSpan(session)}`}}
                    >
                      <Card className="w-full flex-grow flex flex-col shadow-none">
                        <CardContent className="p-2 flex-grow flex flex-col gap-1">
                          <div className="flex justify-between items-center text-xs">
                            <p className="text-muted-foreground">{session.time}</p>
                            <p className="font-medium">{session.type}</p>
                          </div>
                          <div className="my-1">
                            <Badge variant="secondary" className="font-normal">{session.specialization}</Badge>
                          </div>
                          <Separator className="my-1"/>
                          <div className="flex-grow overflow-y-auto pr-1">
                            {session.students && session.students.length > 0 ? (
                              <ul className="space-y-2 text-xs">
                                {session.students.map((student: SessionStudent) => (
                                  <li key={student.id} className={cn("flex justify-between items-center p-1 rounded", student.pendingRemoval && "opacity-50")}>
                                    <div className="flex items-center gap-2">
                                      <User className="h-3 w-3 shrink-0" />
                                      <span className="font-medium">{student.name}</span>
                                      {student.pendingRemoval && <Hourglass className="h-3 w-3 text-destructive" />}
                                    </div>
                                  </li>
                                ))}
                              </ul>
                             ) : (
                                <div className="flex-grow flex items-center justify-center">
                                    <Button variant="ghost" size="sm" className="text-xs text-muted-foreground font-normal">
                                        <UserPlus className="mr-2 h-3 w-3" /> Add Student
                                    </Button>
                                </div>
                             )}
                          </div>
                        </CardContent>
                      </Card>
                    </div>
                  );
              }
              
              return (
                <div key={`${day}-${time}`} className="h-28 border-t"></div>
              );
            })}
          </div>
        </div>
      ))}
    </div>
  );
};


export default function DashboardPage() {
    const { user } = useAuth();
    const [semesters, setSemesters] = useState<Semester[]>([]);
    const [loading, setLoading] = useState(true);
    const [selectedSemesterId, setSelectedSemesterId] = useState<string>("");
    const [selectedTeacher, setSelectedTeacher] = useState<string>("");

    useEffect(() => {
        setLoading(true);
        const unsub = onSnapshot(collection(db, 'semesters'), (snapshot) => {
            const allSemesters = snapshot.docs.map(doc => ({ id: doc.id, ...doc.data() } as Semester));
            setSemesters(allSemesters);
            if (allSemesters.length > 0 && !selectedSemesterId) {
                const defaultSemester = allSemesters.find(s => s.id === 'fall-2024') || allSemesters[0];
                setSelectedSemesterId(defaultSemester.id);
            }
            setLoading(false);
        }, (error) => {
            console.error("Error fetching semesters: ", error);
            setLoading(false);
        });
        return () => unsub();
    }, []); // removed selectedSemesterId from dependency array

    const selectedSemester = useMemo(() => semesters.find(s => s.id === selectedSemesterId), [semesters, selectedSemesterId]);
    
    const availableTeachers = useMemo(() => {
        return user?.activeRole === 'teacher' 
            ? selectedSemester?.teachers.filter(t => t === user.name) || []
            : selectedSemester?.teachers || [];
    }, [selectedSemester, user]);

    useEffect(() => {
        if (selectedSemester && availableTeachers.length > 0) {
            if (user?.activeRole === 'teacher' && user.name) {
                setSelectedTeacher(user.name);
            } else if (!selectedTeacher || !availableTeachers.includes(selectedTeacher)) {
                setSelectedTeacher(availableTeachers[0]);
            }
        }
    }, [selectedSemester, availableTeachers, user, selectedTeacher]);

    const handleSemesterChange = (semesterId: string) => {
        setSelectedSemesterId(semesterId);
        setSelectedTeacher("");
    };

    if (loading) {
        return (
            <div className="flex h-[calc(100vh-8rem)] items-center justify-center">
                <Loader2 className="h-8 w-8 animate-spin text-primary" />
            </div>
        );
    }
    
    return (
        <div className="space-y-4">
            <div className="flex items-center justify-between">
                <h1 className="text-2xl font-headline">Admin Dashboard</h1>
                <div className="flex items-center gap-2">
                    <Button variant="outline"><UserPlus/> Enroll Student</Button>
                    <Button variant="outline"><Upload/> Import Schedule</Button>
                    <Button variant="outline"><FileDown/> Export PDF</Button>
                    <Button variant="outline"><FileDown/> Export CSV</Button>
                </div>
            </div>
            
            <Card>
                <CardContent className="p-4">
                    <div className="flex flex-col md:flex-row items-center gap-4">
                        <div className="flex items-center gap-2">
                            <BarChart3 className="w-5 h-5 text-muted-foreground" />
                            <Select
                                value={selectedSemesterId}
                                onValueChange={handleSemesterChange}
                            >
                              <SelectTrigger className="w-[180px]">
                                <SelectValue placeholder="Select a semester" />
                              </SelectTrigger>
                              <SelectContent>
                                {semesters.map(s => <SelectItem key={s.id} value={s.id}>{s.name}</SelectItem>)}
                              </SelectContent>
                            </Select>
                        </div>
                        <div className="flex items-center gap-2">
                             <Users className="w-5 h-5 text-muted-foreground" />
                             <Select
                                value={selectedTeacher}
                                onValueChange={setSelectedTeacher}
                                disabled={!selectedSemester || user?.activeRole === 'teacher'}
                            >
                              <SelectTrigger className="w-[180px]">
                                <SelectValue placeholder="Select a teacher" />
                              </SelectTrigger>
                              <SelectContent>
                                {availableTeachers.map(t => <SelectItem key={t} value={t}>{t}</SelectItem>)}
                              </SelectContent>
                            </Select>
                        </div>
                        <div className="flex items-center gap-2">
                             <Calendar className="w-5 h-5 text-muted-foreground" />
                             <ToggleGroup type="single" defaultValue="all" variant="outline" size="sm">
                                <ToggleGroupItem value="all" aria-label="Toggle all">All</ToggleGroupItem>
                                <ToggleGroupItem value="sat" aria-label="Toggle sat">Sat</ToggleGroupItem>
                                <ToggleGroupItem value="sun" aria-label="Toggle sun">Sun</ToggleGroupItem>
                                <ToggleGroupItem value="mon" aria-label="Toggle mon">Mon</ToggleGroupItem>
                                <ToggleGroupItem value="tue" aria-label="Toggle tue">Tue</ToggleGroupItem>
                                <ToggleGroupItem value="wed" aria-label="Toggle wed">Wed</ToggleGroupItem>
                                <ToggleGroupItem value="thu" aria-label="Toggle thu">Thu</ToggleGroupItem>
                             </ToggleGroup>
                        </div>
                        <div className="flex items-center gap-2 md:ml-auto">
                            <Calendar className="w-5 h-5 text-muted-foreground" />
                            <span className="text-sm text-muted-foreground">Week of:</span>
                            <div className="flex items-center gap-1">
                                <Button variant="ghost" size="icon" className="h-8 w-8"><ChevronLeft /></Button>
                                <span className="text-sm font-medium">Jun 28-3, 2025</span>
                                <Button variant="ghost" size="icon" className="h-8 w-8"><ChevronRight /></Button>
                            </div>
                        </div>
                    </div>
                </CardContent>
            </Card>

            <div className="overflow-x-auto">
                <ScheduleGrid semester={selectedSemester} teacher={selectedTeacher} />
            </div>
        </div>
    );
}
